.reviews__container {
  position: relative;
  background-color: $color-default-white;
  padding: 111px 403px 120px;

  @include vp-1200 {
    padding: 90px 100px 100px;
  }

  @include vp-767 {
    padding: 57px 47px 70px;
  }
}

.reviews__title {
  padding: 0;
  margin: 0;
  @include section-title;
  color: $color-primary;
  margin-bottom: 26px;
  text-align: center;

  @include vp-1200 {
    margin-bottom: 39px;
  }

  @include vp-767 {
    margin-bottom: 35px;
  }
}

.reviews__card {
  display: flex;
  background-color: #1c3374;
  color: $color-default-white;
  width: 100%;

  @include vp-767 {
    display: block;
    position: relative;
  }
}

.reviews__card-photo {
  padding: 62px 0 100px 62px;
  min-width: max-content;

  @include vp-1200 {
    padding: 62px 0 100px 65px;
  }

  @include vp-767 {
    padding: 0;
    position: absolute;
    top: 40px;
    left: 41px;
  }
}

.reviews__image-container {
  display: block;
  border-radius: 50%;
  width: 80px;
  height: 80px;

  @include vp-767 {
    width: 68px;
    height: 68px;
  }
}

.reviews__image {
  display: block;
  border-radius: 50%;
  width: 80px;
  height: 80px;

  @include vp-767 {
    width: 68px;
    height: 68px;
  }
}

.reviews__card-description-container {
  padding: 54px 62px 58px 28px;

  @include vp-1200 {
    padding: 56px 65px 57px 28px;
  }

  @include vp-767 {
    padding: 51px 25px 36px;
  }

  & span {
    color: $color-default-white;
    font-size: 20px;
    font-family: $font-family-secondary;
    font-weight: 700;
    line-height: 150%;
    display: block;
    margin-bottom: 10px;

    @include vp-767 {
      margin-bottom: 20px;
      margin-left: 100px;
      line-height: 123%;
    }
  }

  & p {
    margin: 0;
    padding: 0;
    color: $color-default-white;
    font-size: 16px;
    font-family: $font-family-secondary;
    font-weight: 400;
    line-height: 140%;

    @include vp-767 {
      text-align: center;
    }
  }
}
